.header {
  position: relative;
  display: flex;
  justify-content: center;
  min-height: 56px;
  background-color: #ffffff;
}
.header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}
.header__logo:hover {
  stroke: #f2f2f2;
}
.header__logo:focus-visible {
  outline: 2px solid #000000;
  outline-offset: 7px;
  border-radius: 3px;
}
.header__logo:active {
  stroke: #a3ffc5;
}
.header__button {
  position: relative;
  top: 17px;
  left: 128px;
  width: 23px;
  height: 22px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.header__button:hover {
  opacity: 0.6;
}
.header__button:focus-visible {
  outline: 2px solid #000000;
  outline-offset: 7px;
  border-radius: 3px;
}
.header__button:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .header__logo {
    z-index: 3;
