}
.feedback-radio__item {
  position: relative;
}
.feedback-radio__label {
  text-transform: none;
}
.feedback-radio__button,
.feedback-radio__custom-checkbox {
  position: relative;
  z-index: -1;
  opacity: 0;
}
.feedback-radio__button-custom {
  position: absolute;
  top: -12px;
  left: -35px;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 50%;
  cursor: pointer;
}
.feedback-radio__button-custom::before {
  content: "";
  display: inline-block;
  width: 16px;
  height: 16px;
  background-color: rgba(51, 51, 51, 0.3);
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.2s ease-in;
}
.feedback-radio__button:checked + .feedback-radio__button-custom::before {
  transform: translate(-50%, -50%) scale(1);
}
.feedback-radio__button-custom:hover {
  background-color: #e5e5e5;
}
.feedback-radio__button:checked + .feedback-radio__button-custom:hover::before {
  background-color: #000000;
  background-position: center;
  background-repeat: no-repeat;
}
.feedback-radio__button:focus-visible + .feedback-radio__button-custom::before {
  outline: #000000 solid 2px;
  outline-offset: 14px;
}
.feedback-radio__button:focus-visible:not(:checked) + .feedback-radio__button-custom::before {
  outline: #000000 solid 2px;
  outline-offset: 14px;
}
.feedback-radio__label--checkbox {
  cursor: pointer;
  position: relative;
  top: -16px;
  left: -52px;
}
.feedback-radio__label--checkbox::before {
  position: relative;
  left: -16px;
  top: 15px;
  content: "";
  display: inline-block;
  width: 46px;
  height: 46px;
  border: none;
  border-radius: 4px;
  background-color: #f2f2f2;
}
.feedback-radio__custom-checkbox {
  position: relative;
  bottom: 16px;
  left: -31px;
}
.feedback-radio__button {
  bottom: 0;
  right: 23px;
}
.feedback-radio__custom-checkbox:checked + .feedback-radio__label--checkbox::before {
  background-color: #d5d5d5;
  background-image: url("../img/vector/checkbox-grey.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.feedback-radio__custom-checkbox:not(:disabled):not(:checked) + .feedback-radio__label--checkbox:hover::before {
  background-color: #e5e5e5;
}
.feedback-radio__custom-checkbox:checked + .feedback-radio__label--checkbox:hover::before {
